                            title: '四豹',
                            value: 0,
                            key: 'Shuang'
                        },
                        {
                            title: '小牛',
                            value: 0,
                            key: 'Shuang'
                        },
                        {
                            title: '无牛',
                            value: 0,
                            key: 'Shuang'
                        }
                    ]
                },
                {
                    title: '翻倍(闲)',
                    list: [
                        {
                            title: '牛1',
                            value: 0,
                            key: 'Da'
                        },
                        {
                            title: '牛2',
                            value: 0,
                            key: 'Xiao'
                        },
                        {
                            title: '牛3',
                            value: 0,
                            key: 'Dan'
                        },
                        {
                            title: '牛4',
                            value: 0,
                            key: 'Shuang'
                        },
                        {
                            title: '牛5',
                            value: 0,
                            key: 'Da'
                        },
                        {
                            title: '牛6',
                            value: 0,
                            key: 'Xiao'
                        },
                        {
                            title: '牛7',
                            value: 0,
                            key: 'Dan'
                        },
                        {
                            title: '牛8',
                            value: 0,
                            key: 'Shuang'
                        },
                        {
                            title: '牛9',
                            value: 0,
                            key: 'Da'
                        },
                        {
                            title: '牛牛',
                            value: 0,
                            key: 'Xiao'
                        },
                        {
                            title: '五公',
                            value: 0,
                            key: 'Dan'
                        },
                        {
                            title: '四豹',
                            value: 0,
                            key: 'Shuang'
                        },
                        {
                            title: '小牛',
                            value: 0,
                            key: 'Shuang'
                        },
                        {
                            title: '无牛',
                            value: 0,
                            key: 'Shuang'
                        }
                    ]
                }
            ],
            isLoading: false
        }
    },
    computed: {
        currentLimit: function() {
            let that = this,
                obj = that.gameList.length ? that.gameList.find(item => item.desc == that.selectGame).limit : [];

            return obj.length === 0 ? that.tenLimit : obj;
        },
        currentOdds: function() {
            let that = this,
                obj = that.gameList.length ? that.gameList.find(item => item.desc == that.selectGame).odds : [];

            return obj.length === 0 ? that.tenLimit : obj;
        }
    },
    mounted() {
        let that = this;

        that.showLoading = true;
        that.getGameList().then(function() {
            // that.gameList.push({
            //     title: '百家乐',
            //     value: 1,
            //     desc: 'bjl',
            //     key: 'video',
            //     type: 3,
            //     limitId: '',
            //     oddsId: '',
            //     limit: [],
            //     odds: []
            // });
            that.gameList.forEach(item => {
                that.getLimitData(item.value, item.key);
                that.getOddsData(item.value, item.key);
            })
        })
    },
    methods: {
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                that.$axios({
                    url: '/api/Merchant/GetGameList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.gameList = result.map(item => {
                            return {
                                title: item.GameName,
                                value: item.GameType,
                                desc: item.NickName,
                                type: item.Type,
                                key: 'lottery',
                                limitId: '',
                                oddsId: '',
                                limit: [],
                                odds: []
                            }
                        }).sort((a, b) => a.value - b.value);
                        resolve();
                    }
                    reject();
                })
            })
        },
        changeGame() {
            let that = this;
        },
        async handleSaveAll() {
            let that = this;

            that.showLoading = true;
            that.handleSave('Limit').then(function() {
                that.handleSave('Odds').then(function(data) {
                    if (data.status === 100) {
                        that.$Message.success(data.message);
                    } else {
                        that.$Message.error(data.message);
                    }
                    that.getLimitData(data.type, data.key);
                    that.getOddsData(data.type, data.key);
                })
            });
        },
        handleSave(key) {
            let that = this,
                param = {},
                vData = {},
                path = '',
                res = null;

            return new Promise(function(resolve, reject) {
                res = that.gameList.find(ele => ele.desc === that.selectGame);
                param['gameType'] = res.value;


                if (key.indexOf('Limit') !== -1) {
                    param['id'] = res.limitId;
                    that.currentLimit.forEach(item => {
                        if (item.key !== 'AllLimit') {
                            vData[item.key] = {
                                MinBet: item.personMinBet,
                                MaxBet: item.personMaxBet,
                                AllMaxBet: item.allMaxBet,
                                SingleBet: item.singleHand
                            }
                        } else {
                            vData['TotalSingleLimit'] = item.personMaxBet;
                            vData['AllTotalQuotas'] = item.allMaxBet;
                        }
                    })

                    switch(res.key) {
                        case 'lottery':
                            path = '/api/Merchant/UpdateBetLimitInfo';
                            break;
                        case 'video':
                            path = '/api/Merchant/UpdateVideoBetLimitInfo';
                            break;
                    }
                } else {
                    param['id'] = res.oddsId;
                    let obj = that.currentOdds;

                    for (let item in obj) {
                        obj[item].list.forEach(opt => {
                            vData[opt.key] = opt.value;
                        })
                    }

                    switch(res.key) {
                        case 'lottery':
                            path = '/api/Merchant/UpdateOddsInfo';
                            break;
                        case 'video':
                            path = '/api/Merchant/UpdateVideoOddsInfo';
                            break;
                    }
                }

                if (!that.isLoading) {
                    that.isLoading = true;

                    that.$axios({
                        url: path,
                        method: 'post',
                        params: param,
                        data: vData
                    }).then(function(data) {
                        that.isLoading = false;
                        resolve({
                            status: data.data.Status,
                            message: data.data.Message,
                            type: res.value,
                            key: res.key
                        })
                    })
                }
            })
        },
        getLimitData(type, key) {
            let that = this,
                obj = [],
                href = '';

            switch(key) {
                case 'lottery':
                    href = '/api/Merchant/GetBetLimitInfo';
                    break;
                case 'video':
                    href= '/api/Merchant/GetVideoBetLimitInfo';
                    break;
            }

            that.$axios({
                url: href,
                params: {
                    gameType: type
                }
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Model;
                    const gameInfo = that.gameList.find(item => item.value == type && item.key === key);

                    switch(gameInfo.type) {
                        case 1:
                            obj = JSON.stringify(that.tenLimit);
                        break;
                        case 2:
                            obj = JSON.stringify(that.fiveLimit);
                        break;
                        case 3:
                            obj = JSON.stringify(that.bjlLimit);
                        break;
                    }
                    obj = JSON.parse(obj);
                    obj.forEach(item => {
                        if (result[item.key]) {
                            item.personMinBet = result[item.key].MinBet;
                            item.personMaxBet = result[item.key].MaxBet;
                            item.allMaxBet = result[item.key].AllMaxBet;
                            item.singleHand = result[item.key].SingleBet;
                        } else if (item.key === 'AllLimit') {
                            item.personMaxBet = result.TotalSingleLimit;
                            item.allMaxBet = result.AllTotalQuotas;
                        }
                    })
                    gameInfo.limit = obj;
                    gameInfo.limitId = result.ID;
                //     const result = data.data.Model;
                //     const gameInfo = that.gameList.find(item => item.value == type && item.key === key);
                //     for (let key in result) {
                //         let value = result[key];
                //         if (key != 'TotalSingleLimit' && key != 'AllTotalQuotas' && key != 'ID') {
                //             let keyText = key.match(/[a-zA-Z]+/g)[0];
                //             let keyText_ = that.titleList[keyText];
                //             obj.push({
                //                 title: keyText_ ? (key.slice(keyText.length) ? keyText_.replace('&', key.slice(keyText.length)) : keyText_.replace('【&】', '')) : '',
                //                 key: key,
                //                 personMinBet: value.MinBet,
                //                 personMaxBet: value.MaxBet,
                //                 allMaxBet: value.AllMaxBet,
                //                 singleHand: value.SingleBet
                //             })
                //         }
                //     }
                //     obj.push({
                //         title: '总限额',
                //         key: 'AllLimit',
                //         personMaxBet: result.TotalSingleLimit,
                //         allMaxBet: result.AllTotalQuotas
                //     })
                //     gameInfo.limit = obj;
                //     gameInfo.limitId = result.ID;
                }
                that.isLoading = false;
                that.showLoading = false;
            })
        },
        getOddsData(type, key) {
            let that = this,
                obj = null,
                href = '';

            switch(key){
                case 'lottery':
                    href = '/api/Merchant/GetOddsInfo';
                    break;
                case 'video':
                    href = '/api/Merchant/GetVideoOddsInfo';
                    break;
            }

            that.$axios({
                url: href,
                params: {
                    gameType: type
                }
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Model;
                    const gameInfo = that.gameList.find(item => item.value == type && item.key === key);

                    switch(gameInfo.type) {
                        case 1:
                            obj = JSON.stringify(that.tenOdds);
                        break;
                        case 2:
                            obj = JSON.stringify(that.fiveOdds);
                        break;
                        case 3:
                            obj = JSON.stringify(that.bjlOdds);
                        break;
                    }
                    obj = JSON.parse(obj);
                    for (let item in obj) {
                        obj[item].list.forEach(opt => {
                            opt.value = result[opt.key];
                        })
                    }
                    gameInfo.odds = obj;
                    gameInfo.oddsId = result.ID;
                }
            })
        }
    },
}
</script>

